/* Container needed to position the overlay. Adjust the width as needed */
.preview {
    position: relative;
    width: 100%;
    /*max-width: 300px;*/
    margin-bottom: 20px;
}

/* Make the image to responsive */
.preview-pic {
    display: block;
    width: 100%;
    height: auto;
}

/* The overlay effect - lays on top of the container and over the image */
.intro {
    position: absolute;
    bottom: 0;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5); /* Black see-through */
    color: #f1f1f1;
    width: 100%;
    transition: .5s ease;
    opacity:1;
    color: white;
    font-size: 20px;
    padding: 20px;
    text-align: center;
}
